//    subscribe       .  subscribe   . ( observer )
const intervalObsvr = new Observable(observer=>{ // create   ,    
    console.log('start subscriber...');
    let i = 0;

    setInterval(()=>{
        observer.next(i++);
        //observer.error(new Error("got error"))
        observer.complete();
    }, 1000);

});

